.parallax__passthrough-container {
  position: relative;
  width: 100%;
  height: 500px; }
  .parallax__passthrough-container:before {
    content: '';
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    z-index: 1;
    background: linear-gradient(0deg, rgba(0, 0, 0, 0.3) 0%, transparent 50%); }
  .parallax__passthrough-container .parallax__passthrough-bkg {
    position: absolute;
    background-size: cover !important;
    width: 100%;
    height: 500px;
    background-position: center top; }
    @media (min-width: 992px) {
      .parallax__passthrough-container .parallax__passthrough-bkg {
        background-attachment: fixed !important; } }

